import { useState, useRef } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { IMaskInput } from 'react-imask';
import {helpers} from '../../reusable';

import './ModificationsList.css'




const Config = () => {
  const ref = useRef(null);
  const inputRef = useRef(null);
  const [userName, setUserName] = useState('');
  const [userPhone, setUserPhone] = useState('');
  const navigate = useNavigate();

  const handlesubmit = (e) => {
    e.preventDefault();
    if (userPhone.length === 11) {
      navigate("/chinese-cars-store/success");
    }

  }

  const [toggleConfig, setToggleConfig] = useState(false);
  const [rotateChevron, setRotateChevron] = useState(false);
  const handleRotate = () => setRotateChevron(!rotateChevron);
  const width = helpers.useWindowWidth();

  return (
    <div className='modifConfig__container'>
      <div className="modifGrid">
        <div onClick={() => { setToggleConfig(!toggleConfig); handleRotate() }} className='configDropdownBtn'>
          <svg className={` ${rotateChevron ? "rotate" : 'rotateBack'}`} width="14" height="9" viewBox="0 0 16 9" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M7.29289 8.70711C7.68342 9.09763 8.31658 9.09763 8.70711 8.70711L15.0711 2.34315C15.4616 1.95262 15.4616 1.31946 15.0711 0.928932C14.6805 0.538408 14.0474 0.538408 13.6569 0.928932L8 6.58579L2.34315 0.928932C1.95262 0.538408 1.31946 0.538408 0.928932 0.928932C0.538408 1.31946 0.538408 1.95262 0.928932 2.34315L7.29289 8.70711ZM7 7L7 8L9 8L9 7L7 7Z" fill={'var(--color-text)'} />
          </svg>
          <h4 className='p__carPrice-Title'>Comfort</h4>
        </div>
        <h4 className='bold textCenter modifBigScreen'>3</h4>
        <h4 className='lineThrough  modifBigScreen_textleft'>от 2 080 000 ₽</h4>
        <h3 className='bold modifBigScreen_textleft'>от 1 830 000 ₽</h3>
        {( width >= 1000 || !toggleConfig ) && <a href="#" className='modifBtn fade'>Купить в кредит</a>} 
      </div>
      {toggleConfig && <div className='modifDropdown slide-down'>
        <div className='modifDescription'>
          <div>
            <h5>Экстерьер</h5>
            <ul>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
            </ul>
          </div>
          <div>
            <h5>Безопасность</h5>
            <ul>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
            </ul>
          </div>
          <div>
            <h5>Интерьер</h5>
            <ul>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
            </ul>
          </div>
          <div>
            <h5>Комфорт</h5>
            <ul>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
            </ul>
          </div>
          <div>
            <h5>Мультимедиа</h5>
            <ul>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
              <li>Фронтальные подушки безопасности</li>
            </ul>
          </div>
        </div>
        <div >
          <h5>Решение по автокредиту за 15 минут оформите заявку сейчас!</h5>
          <form onSubmit={(e) => { handlesubmit(e) }}>
            <div className='configForm'>
              <div className='input-container'>
                <input
                  id='userName-configForm'
                  type='text'
                  role='textbox'
                  placeholder='ФИО'
                  value={userName}
                  required
                  onChange={(e) => setUserName(e.target.value)}
                />
              </div>
              <div className='input-container'>
                <IMaskInput
                  id='userPhone-configform'
                  mask={'+{7}(000) 000-00-00'}
                  value={userPhone}
                  unmask={true}
                  ref={ref}
                  inputRef={inputRef}
                  minlength="17"
                  required
                  onAccept={(value) => { setUserPhone(value) }}
                  placeholder='+7 (___)___-__-__'
                />
              </div>
              <div>
                <button><h4>Получить предложение</h4></button>
              </div>
            </div>
            <p>Нажимая кнопку “Получить предложение” я соглашаюсь с <Link to='/chinese-cars-store/politics' className='underline'>политикой конфиденциальности</Link></p>
          </form>
        </div>
      </div>
      }

    </div>
  )
}




const ModificationsList = () => {

  return (
    <div className='section__padding-noY'>
      <div className='modifContainer modifGrid'>
        <h4>1.5 7DCT (150 л.с.) 2WD <span className='bgAccent__TextWhite'>-25%</span></h4>
        <p className='textCenter modifBigScreen'>В наличии</p>
        <p className='textCenter modifBigScreen'>Розничная цена</p>
        <p className='textCenter modifBigScreen'>Цена со скидкой</p>
        <p className='bold textCenter modifBigScreen'>Цены актуальны до 20 марта</p>
      </div>

      <Config />
    </div>
  )
}

export default ModificationsList