import {DiscountPannel, CreditFormShort, NiceOptions, BannerForm, Characteristics, ModificationsList, HeaderModel, Gallery} from '../../components'

const SpecificCarModel = () => {
  return (
    <div>
      <Gallery/>
      <HeaderModel type={'car'}/>
      <HeaderModel type={'model'}/>
      <DiscountPannel/>
      <h2 className='section__padding-noY uppercase' >Комплектации и цены <span className='colorAccent'>GEELY TUGELLA</span></h2>
      <ModificationsList/>
      <Characteristics/>
      <BannerForm/>
      <h2 className='section__padding-noY' >Купить этот авто в кредит по<br/>спецпрограмме <span className='colorAccent'>LADA FINANCE</span></h2>
      <CreditFormShort/>
      <h2 className='section__padding-noY uppercase' >Возможно вас<br/>заинтересует</h2>
      <NiceOptions/>
    </div>
  )
}

export default SpecificCarModel